import * as api from '../api';


export const postUser = (user) => async (dispatch) => {
    try {
        const { data } = await api.createUser(user);
        dispatch({type: 'SEND', payload: data});
    } catch (error) {
        console.log(error);
    }
}

export const validateUser = (user) => async (dispatch) => {
    try{
        //await api.askForUser(user);
        const { data } = await api.getUser(user);
        console.log("hello?");
        console.log(data);
        dispatch({type: 'GET_USER', payload: data});
    } catch(error){
        console.log(error);
    }
}

export const uniqueUser = (user) => async (dispatch) => {
    try{
        const { data } = await api.isUniqueUser(user);
        console.log("hello?");
        console.log(data);
        dispatch({type: 'GET_USER', payload: data});
    } catch (error){
        console.log(error);
    }
}