import axios from 'axios';


const url =  'http://react-hero2.herokuapp.com';
const url2 = 'http://react-hero2.herokuapp.com/user';
const url3 = 'http://react-hero2.herokuapp.com/checkuser'
const url4 = 'http://react-hero2.herokuapp.com/checkusername'

export const fetchPosts = () => axios.get(url);
export const createPost = (newPost) => axios.post(url, newPost);
export const createUser = (newUser) => axios.post(url2, newUser);


//validate user
export const getUser = async (user) => 
    axios.post(url3, user);
    axios.get(url3);

export const isUniqueUser = async (user) =>
    axios.post(url4, user);
    axios.get(url4);

