import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { GoogleLogin } from 'react-google-login';
import { useDispatch } from 'react-redux';
import { validateUser } from '../../actions/users';
import { useSelector } from 'react-redux';
const Login = () => {
    const [userData, setUserData] = useState({
        name: '', password: ''
    });
    const [validUser, setValidUser] = useState({
        valid: true
    })

    const dispatch = useDispatch();
    const history = useHistory();
    useEffect(
        ()=> {dispatch(validateUser(userData))},
         [dispatch]);
    const isValid = useSelector((state) => state.users.count);
    console.log(isValid);
    if(isValid === 1 && userData.name != ''){
        
        const result = {name: userData.name};
        try {
            dispatch({type: 'AUTH', data: {result} });
            history.push('/react-hero');
        } catch (error) {
            console.log(error);
        } 
    } 

    const handleChange = () => {

    }
    const submit = (e) =>{
        e.preventDefault();
        dispatch(validateUser(userData));
        if(userData.name != ''){
            setValidUser({valid: false});
        }
    }
    
    const googleFailure =  (error) => {
        console.log("failure");
        console.log(error);
    }
    const googleSuccess = async (res) => {
        // ?. means that if res doesnt exist, accessing profileObj of it wont throw an error
        const result = res?.profileObj;
        const token = res?.tokenId;

        try {
            dispatch({type: 'AUTH', data: {result, token} });
            console.log(res);
            history.push('/react-hero');
        } catch (error) {
            console.log(error);
        }
        
    }

        return (
        <div style={{textAlign: 'center'}}>
            <h1>Login</h1>
            <form onSubmit = {submit}>
            <label style={{marginRight: '2vw'}}><h4>Username:</h4></label>
            <input
                required 
                type="text" 
                id="fname" 
                name="fname" 
                value={userData.name} 
                onChange = {(e)=> setUserData({...userData, name: e.target.value })}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h4>Password:</h4></label>
            <input 
                required
                type="password" 
                id="password" 
                name="password" 
                value={userData.password} 
                onChange = {(e)=> setUserData({...userData, password: e.target.value })}
            />
            <br/>
            <input type = "submit" value="Submit"/>   
        </form>
        <br/>
        {!validUser.valid && isValid != 1 ? <h4 style={{marginTop: '1vw', color: 'red'}}>Invalid Username or password</h4> : <div></div> }
            <br/>
                <GoogleLogin 
                    clientId="585414485062-c0ivjqpj0o4hm95l8it2nbqomvioi5j4.apps.googleusercontent.com"
                    render={(renderProps)=>(
                    <button 
                        onClick={renderProps.onClick} 
                        disabled={renderProps.disabled}
                    >Login With Google</button>
                    )}
                    onFailure={googleFailure}
                    onSuccess={googleSuccess}
                    cookiePolicy="single_host_origin"
                />
            <br />
            <h5>
                <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/create">Don't have an account? Create one here!</Link>
            </h5>
        </div>
        );
}

export default Login;