import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { postUser, uniqueUser } from '../../actions/users';
import { useSelector } from 'react-redux';


const Create  = () => {
    
    const [userData, setUserData] = useState({
        name: '', email: '', password: '', confirmPassword: ''
    });
    const [validUser, setValidUser] = useState({
        valid: 0
    });
    const dispatch = useDispatch();
    const history = useHistory();
    console.log(userData);
    dispatch(uniqueUser(userData));
    /*useEffect(
        ()=> {dispatch(uniqueUser(userData))},
         [dispatch]);*/
    const isValid = useSelector((state) => state.users.count2);
    console.log(isValid);
    const submit = async (e) => {
        e.preventDefault();
        dispatch(uniqueUser(userData));

        if(isValid === 0){
            dispatch(postUser(userData));
            const result = {name : userData.name};
            dispatch({type: 'AUTH', data: {result} });
            history.push('/react-hero');
        }
    

    }
     return (
        <div style={{textAlign: 'center'}}>
            <h1 style = {{fontSize:'32px'}}>Create an account</h1>
            <form style={{ textAlign: 'right'}}onSubmit = {submit}>
            <div style={{paddingRight: 'calc(50% - 150px)'}}>
            <label style={{marginRight: '2vw'}}><h4>Username:</h4></label>
            <input 
                required
                type="text" 
                id="fname" 
                name="fname" 
                value = {userData.name} 
                onChange = {(e)=> setUserData({...userData, name: e.target.value })}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h4>Email: </h4></label>
            <input 
                required
                type="email" 
                id="email" 
                name="email"  
                value = {userData.email} 
                onChange = {(e)=> setUserData({...userData, email: e.target.value })}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h4>Password:</h4></label>
            <input 
                required
                type="password" 
                id="password" 
                name="password"  
                value = {userData.password} 
                onChange = {(e)=> setUserData({...userData, password: e.target.value })}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h4>Confirm Password:</h4></label>
            <input 
                required
                type="password" 
                id="confirm-password" 
                name="confirm-password"
                value = {userData.confirmPassword}   
                onChange = {(e)=> setUserData({...userData, confirmPassword: e.target.value })}
            />
            <br/>
            </div>
            <div style={{textAlign:'center'}}> <input type = "submit" value="Submit"/>  </div>
            
        </form>
            <br/>
            {!validUser.valid && isValid != 0 ? <h4 style={{marginTop: '1vw', color: 'red'}}>Username already taken.</h4> : <div></div> } 
            <br />
            <h5>
                <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/login">Already have an account? Login here!</Link>
            </h5>
        </div>
    );
}

export default Create;